<script>
import { onMount } from 'svelte';
import router, { curRoute, originalPath } from './routes/router.js';
import { detectWindow, setDarkTheme, toggleTheme, toggleSafeSearch, getLocalStorageTheme, getLocalSafeSearch, clearSiteData, checkPageAndSafeParams } from './components/Utils.svelte';
// INTERNATIONALIZATION SUPPORT
import { message } from './messages.js';
let msg = message;
let stopP = false;
$: isMobile = true;


// Default search parameters
let data = {
	query: "",
	page: 1,
	safe: getLocalSafeSearch(),
	resp: []
}

function setQuery(query) {
	query = query.trim();
	if (query.length > 0 && query.length <= 100) {
		data.query = encodeURIComponent(query.trim()).replace(/%20/g, '+');
		return true;
	} else {
		return false;
	}
}

function getQuery() {
	return decodeURIComponent(data.query).replace(/\+/g, ' ');
}

function setPage(page) {
	if (!isNaN(page) && page <= 5) {
		data.page = parseInt(page);
		return true;
	} else {
		return false;
	}
}

function updatePageTitle(isDefault) {
	if (isDefault === true) {
		window.document.title = msg.documentTitle;
	} else {
		window.document.title = getQuery() + msg.searchTitle;
	}
}

function handlerBackNavigation(event) {
	if (event && event.state && event.state.path) {
		var toAdd = true;
		if (window.location.search) {
			const urlParams = new URLSearchParams(window.location.search);
			if (urlParams) {
				const tmpQ = urlParams.get('query');
				const tmpP = urlParams.get('page');
				if (tmpQ && tmpP) {
					setQuery(tmpQ)
					setPage(tmpP)
					toAdd = false;
					updatePageTitle(false);
					search(true, true, false);
				}
			}
		}
		if (toAdd === true ){
			updatePageTitle(true);
			curRoute.set(event.state.path);	// will only navigate if value found
		}
	}
}

function redirectTo(path, pushState) {
	try {
		// console.log(1,$curRoute.match('^/websearch.([-a-zA-Z0-9()@:%_\+.~#?&//=]*)'));
		curRoute.set(path);
		updatePageTitle(false);
		if (pushState === true) {
			window.history.pushState({path: path}, '', window.location.origin + path + '?query=' + data.query + '&page=' + data.page);
		}
	} catch(err) {
		console.log(err.message);
	}
}

function showErrorPage() {
	data.resp = 'SEARCH_ERROR';
	stopP	= false;
}

function search(toRedirect, clearData, pushState) {
	try {
		if (checkPageAndSafeParams(data.page, data.safe)) {
			// Add circuit breaker logic - this prevents multiple search execution within a short period of time
			if (stopP === true) {
				return;
			} else {
				stopP	= true;
				window.setTimeout(() => {
					stopP	= false;
				}, 2500);
			}
			var xhttp = new XMLHttpRequest();
			xhttp.overrideMimeType("application/json");
			xhttp.open("POST", "https://api.getddit.com/api/v1/search");
			// xhttp.onreadystatechange = function () {
			// 	if (xhttp.status == '200' && xhttp.responseText) {
			// 		if (toRedirect === false) {
			// 			var tmp = JSON.parse(xhttp.responseText);
			// 			data.resp.pageNo = tmp.pageNo;
			// 			Array.prototype.push.apply(data.resp.results, tmp.results)
			// 		} else {
			// 			data.resp = JSON.parse(xhttp.responseText);
			// 		}
			// 		stopP	= false;
			// 	}
			// };
			xhttp.onload = function() {
				if (xhttp.responseText) {
					if (toRedirect === false) {
						var tmp = JSON.parse(xhttp.responseText);
						data.resp.pageNo = tmp.pageNo;
						Array.prototype.push.apply(data.resp.results, tmp.results)
					} else {
						data.resp = JSON.parse(xhttp.responseText);
					}
				}
				stopP	= false;
			};
			xhttp.timeout = 10000;
			xhttp.addEventListener('error', showErrorPage);
    	xhttp.addEventListener('timeout', showErrorPage);
    	xhttp.addEventListener('abort', showErrorPage);
			xhttp.setRequestHeader("Content-Type", "application/json");
			if (clearData === true) {
				data.resp = [];
			}
			xhttp.send(
				JSON.stringify(
					{
						query: data.query,
						page: data.page,
						safe: data.safe
					}
				)
			);
			if (toRedirect === true) {
				redirectTo('/websearch', pushState);
			}
		}
	} catch(err) {
		console.log(err.message);
		showErrorPage();
	}
}

function invokeSearch(e) {
	if (stopP === true) {
		return;
	}
	var hasChanges = false;
	if (e.detail.query) {
		hasChanges = setQuery(e.detail.query);
	}
	if (e.detail.page) {
		setPage(e.detail.page)
		hasChanges = true;
	}
	if (hasChanges) {
		if (e.detail.redirect === false) {
			search(false, false, false);		// Used by mobile with infinite scroll
		} else {
			search(true, true, true);		// Used by desktop browsers
		}
	}
}

function sprocessing(e) {
  if (e.key === "Enter") {
    e.preventDefault();
		if (setQuery(e.explicitOriginalTarget.value)) {
			data.page = 1;
			search(true, true, true);
		}
  }
}

function tempToggleSafeSearch(e) {
	if (data.safe === false) {
		data.safe = true;
	} else {
		data.safe = false;
	}
	toggleSafeSearch(e);
}

function closeBurgerMenu(e) {
	var tmp = e.explicitOriginalTarget.id;
	var isClose = true;
	if (tmp) {
		if (tmp === 'burger' || tmp === 'menu' || tmp === 'nan' || tmp === 'themeSwitch' || tmp === 'safeSwitch') {
			isClose = false;
		}
	} else {
		tmp = e.explicitOriginalTarget.wholeText;
		if (tmp) {
			isClose = false;
		}
	}
	if (isClose) {
		document.getElementById('burger').checked = false;
	}
}

// Add event listener that will detect if device has changed to mobile view
function addDetectMobileDeviceEventListener() {
	window.matchMedia('screen and (max-width: 810px)').addListener((mq) => {
		if (mq.matches) {
			isMobile = true;
		} else {
			isMobile = false;
		}
  });
}

function urlNavigator() {
	var isValid = false;
	if (window.location.pathname === '/websearch' && window.location.search && window.location.search.indexOf('?query=') > -1) {
		try {
			var tmp = window.location.search.replace('?query=', '');
			if (tmp) {
				tmp=window.location.search.split('&');
				if (tmp.length >= 1 && tmp.length <=3 && tmp[0].indexOf('?query=') == 0) {
					var tmpQ = tmp[0].substring(tmp[0].indexOf('?query=') + 7, tmp[0].length).trim();
					var tmpP = 1;
					isValid = true;
					if (tmp.length > 1) {
						for (let i = 1; i <= tmp.length - 1; i++) {
							if (tmp[i].indexOf('page=') == 0) {
								// Set page number
								tmpP = tmp[i].substring(tmp[i].indexOf('page=') + 5, tmp[i].length).trim();
							} else if (tmp[i].indexOf('night=') == 0) {
								// Set night mode
								var nVal = tmp[i].substring(tmp[i].indexOf('night=') + 6, tmp[i].length).trim();
								if (!isNaN(nVal) && nVal == 1) {
									setDarkTheme();
								}
							} else {
								// If param not found, immediately break the loop and sent stright to main page
								isValid = false;
								break;
							}
						}
					}
					if (isValid && tmpQ && tmpP && setPage(tmpP) && setQuery(decodeURIComponent(tmpQ).replace(/\+/g, ' '))) {
						search(true, true, false);
						window.history.replaceState({path: 'websearch'}, '', window.location.origin + '/websearch?query=' + data.query + '&page=' + data.page);
					} else {
						isValid = false;
					}
				}

				// tmp = tmp.substring(tmp.indexOf('&page=') + 6, tmp.length);
				// if (tmp) {

				// 	if (window.location.search.indexOf('&page=') > 7) {
				// 		tmpQ = window.location.search.substring(window.location.search.indexOf('?query=') + 7, window.location.search.indexOf('&page=')).trim();
				// 		tmpP = window.location.search.substring(window.location.search.indexOf('&page=') + 6, window.location.search.length).trim();
				// 	} else {
				// 		tmpQ = window.location.search.substring(window.location.search.indexOf('?query=') + 7, window.location.search.length).trim();
				// 		tmpP = 1;
				// 	}
				// 	if (tmpQ && tmpP && setPage(tmpP) && setQuery(decodeURIComponent(tmpQ).replace(/\+/g, ' '))) {
				// 		isValid = true;
				// 		search(true, true, false);
				// 	}
				// }
			}
		} catch(err) {
			console.log(err.message)
		}
	}
	if (isValid === false) {
		updatePageTitle(true);
		curRoute.set('/');
	}
}

// Invoked when clicking the subheader
function subHeaderNavigator() {
	curRoute.set('/');
	updatePageTitle(true);
	window.history.pushState({path: '/'}, '', originalPath);
}

onMount(() => {
	// window.history.pushState({path: '/'}, '', originalPath);
	isMobile = detectWindow();
	if (document.getElementById('searchBar')) {
		document.getElementById('searchBar').focus();
	}
	addDetectMobileDeviceEventListener();
	// search(true, true, true);
});
urlNavigator();
</script>

<!-- Event handler to handle 'back' button events -->
<svelte:window on:popstate={handlerBackNavigation} />

<main>
	<section>
		{#if $curRoute !== '/'}
			<div class="skewed skewed-extra"></div>
		{/if}
		<!-- Desktop theme toggle -->
		<div class="flex items-stretch pr-32 absolute right-0 mt-8">
			{#if isMobile === false && $curRoute === '/'}
					<p class="toggleTxt pr-4 font-bold whitespace-no-wrap">{msg.topbar.night}</p>
					<div>
						{#if getLocalStorageTheme() == true}
							<input hidden type="checkbox" checked id="themeSwitch" name="theme" class="toggleIpt" on:click={toggleTheme}/>
						{:else}
							<input hidden type="checkbox" id="themeSwitch" name="theme" class="toggleIpt" on:click={toggleTheme}/>
						{/if}
						<label for="themeSwitch" class="toggleLbl">Toggle</label>
					</div>

					<!-- Safe search toggle -->
					<p class="toggleTxt pl-16 pr-4 font-bold whitespace-no-wrap">{msg.topbar.safe}</p>
					<div>
						{#if getLocalSafeSearch() === true}
							<input hidden type="checkbox" checked id="safeSwitch" name="safeSwitch" class="toggleIpt" on:click={tempToggleSafeSearch}/>
						{:else}
							<input hidden type="checkbox" id="safeSwitch" name="safeSwitch" class="toggleIpt" on:click={tempToggleSafeSearch}/>
						{/if}
						<label for="safeSwitch" class="toggleLbl">Toggle</label>
					</div>
			{/if}
		</div>
		<nav class="menu-container">

			<!-- burger menu -->
			<input type="checkbox" id="burger" aria-label="Toggle menu"/>
			<span id="nan"></span>
			<span id="nan"></span>
			<span id="nan"></span>
			<!-- menu items -->
			<div class="menu" id="menu" on:mouseout={closeBurgerMenu}>
				<ul id="nan">
					{#if isMobile === true || $curRoute !== '/'}
						<li id="nan" class="flex">
							<p class="pr-8" id="nan">{msg.hamburger.night}</p>&nbsp;&nbsp;&nbsp;&nbsp;
							<div id="nan">
								{#if getLocalStorageTheme() == 'dark' || document.documentElement.getAttribute("data-theme") == 'dark'}
									<input hidden type="checkbox" checked id="themeSwitch" name="theme" class="toggleIpt" on:click={toggleTheme}/>
								{:else}
									<input hidden type="checkbox" id="themeSwitch" name="theme" class="toggleIpt" on:click={toggleTheme}/>
								{/if}
								<label for="themeSwitch" id="nan" class="toggleLbl">Toggle</label>
							</div>
						</li>

						<li id="nan" class="flex">
							<p class="pr-8" id="nan">{msg.hamburger.safe}</p>&nbsp;&nbsp;&nbsp;&nbsp;
							<div id="nan">
								{#if getLocalSafeSearch() === true}
									<input hidden type="checkbox" checked id="safeSwitch" name="safeSwitch" class="toggleIpt" on:click={tempToggleSafeSearch}/>
								{:else}
									<input hidden type="checkbox" id="safeSwitch" name="safeSwitch" class="toggleIpt" on:click={tempToggleSafeSearch}/>
								{/if}
								<label for="safeSwitch" id="nan" class="toggleLbl">Toggle</label>
							</div>
						</li>
					{/if}
					<li id="nan" class="flex">
						<p class="pr-6" id="nan">{msg.hamburger.clear}</p>
						<button class="bg-green-500 hover:bg-green-700 text-white text-sm font-bold py-1 px-3 rounded-full" id="nan" on:click={clearSiteData}>Clear</button>
						<!-- <input hidden type="checkbox" id="clearAll" name="theme" class="toggleIpt" on:click={toggleTheme}/>
						<label for="clearAll" id="nan" class="toggleLbl">Toggle</label> -->
					</li>
				</ul>
			</div>
		</nav>
	</section>


	{#if $curRoute !== '/'}
		<div class="absolute top-0 pt-4 w-full">
			<ul class="justify-between w-full">
				<li class="flex justify-center mt-1">
					<!-- Title -->
					<button class="sub-header font-bold md:left-0 md:px-20 bg-transparent text-5xl" on:click={subHeaderNavigator}>{msg.title.complete}</button>
					<!-- Search secondary - desktop -->
					<span class="hidden md:flex w-full items-center">
						<div class="relative search-icon-t mt-1 md:right-0">
							<svg class="fill-current pointer-events-none w-4 h-4 md:h-6 md:w-6" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
								<path d="M12.9 14.32a8 8 0 1 1 1.41-1.41l5.35 5.33-1.42 1.42-5.33-5.34zM8 14A6 6 0 1 0 8 2a6 6 0 0 0 0 12z"></path>
							</svg>
						</div>
						<input type="search" placeholder="{msg.searchPholder}" id="searchBarT" class="bg-search-bar w-1/2 md:justify-center text-base h-10 transition border focus:outline-none rounded py-1 px-2 pl-10 mr-4 
							appearance-none leading-normal max-w-4xl" maxlength="100" on:keydown={sprocessing} value="">
					</span>
				</li>
				<!-- Search secondary - mobile -->
				<li class="block md:hidden pl-4 pr-4">
					<span class="flex w-full items-center justify-center">
						<svg class="absolute search-icon-s fill-current pointer-events-none w-4 h-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" style="left: 1.6rem;">
							<path d="M12.9 14.32a8 8 0 1 1 1.41-1.41l5.35 5.33-1.42 1.42-5.33-5.34zM8 14A6 6 0 1 0 8 2a6 6 0 0 0 0 12z"></path>
						</svg>
						<input type="search" placeholder="{msg.searchPholder}" id="searchBarT" class="bg-search-bar w-full text-base h-8 transition border focus:outline-none rounded appearance-none py-1 px-2 pl-10 leading-normal" 
							maxlength="100" on:keydown={sprocessing} value="{data.query ? getQuery() : ''}">
					</span>
				</li>
			</ul>
		</div>
	{/if}


	<div class="flex">
		<!-- Body -->
		<div class="w-full">
			<!-- Main Content -->
			<div id="pageContent">
				<!-- Page component updates here -->
				{#if $curRoute === '/websearch'}
				  <svelte:component this={router[$curRoute]} {msg} {...data} {isMobile} on:searching={invokeSearch}/>
				{:else if $curRoute === '/settings'}
					‹p›Value is more than 0.75.‹/p›
				{:else}
					<svelte:component this={router[$curRoute]} {msg} on:searching={invokeSearch}/>
				{/if} 
			</div>
		</div>
	</div>
</main>