<script context="module">
const localStorageThemeName = 'APP_THEME';
const localStorageSafeSearchName = 'APP_SAFESEARCH';

// Adds a little delay when performing a theme transition
let trans = () => {
  document.documentElement.classList.add("transition");
  window.setTimeout(() => {
    document.documentElement.classList.remove("transition");
  }, 1000);
};

// Used for switching between light and dark mode
export function toggleTheme(e) {
  if (getLocalStorageTheme()) {
    trans();
    if (isLSAvail()) {
      window.localStorage.setItem(localStorageThemeName, '0');
    }
    window.document.documentElement.setAttribute("data-theme", "light");
    e.explicitOriginalTarget.checked = false;
  } else {
    trans();
    if (isLSAvail()) {
      window.localStorage.setItem(localStorageThemeName, '1');
    }
    window.document.documentElement.setAttribute("data-theme", "dark");
    e.explicitOriginalTarget.checked = true;
  }
}
export function setDarkTheme() {
  if (isLSAvail() && window.localStorage.getItem('APP_THEME') !== '1') {
    window.localStorage.setItem(localStorageThemeName, '1');
  }
  window.document.documentElement.setAttribute("data-theme", "dark");
}

// Simplified method to obtain the site's current theme
export function getLocalStorageTheme() {
  if (isLSAvail() && window.localStorage.getItem(localStorageThemeName) && window.localStorage.getItem(localStorageThemeName) === "1") {
    return true;
  }
  return false;
}

// Used for switching safe search option
export function toggleSafeSearch(e) {
  if (getLocalSafeSearch()) {
    trans();
    document.documentElement.setAttribute("data-safe", "0");
    window.localStorage.setItem(localStorageSafeSearchName, "0");
    e.explicitOriginalTarget.checked = false;
  } else {
    trans();
    document.documentElement.setAttribute("data-safe", "1");
    window.localStorage.setItem(localStorageSafeSearchName, "1");
    e.explicitOriginalTarget.checked = true;
  }
}

// Simplified method to obtain the site's current safe search setting
export function getLocalSafeSearch() {
  if (isLSAvail() && window.localStorage.getItem(localStorageSafeSearchName) && window.localStorage.getItem(localStorageSafeSearchName) === "1") {
    return true;
  }
  return false;
}

// Used to determine if running on mobile or desktop
export function detectWindow() {
  if (window.matchMedia('screen and (max-width: 810px)').matches) {
    return true;
  } else if (window.matchMedia('screen and (max-width: 1500px)').matches) {
    return false;
  }
  return false;
}

// Clear all site data stored within the user's browser
export function clearSiteData(e) {
  e.explicitOriginalTarget.disabled = true;
  if (isLSAvail()) {
    window.localStorage.clear();
  }
  if (isSSAvail()) {
    window.sessionStorage.clear();
  }
  e.explicitOriginalTarget.disabled = false;
  window.location.href = window.location.origin.concat('/');
}

// Verify search parameters - specifically page and safe
export function checkPageAndSafeParams(page, safe) {
  if (typeof safe === "boolean" && !isNaN(page) && page > 0 && page <= 100) {
    return true;
  } else {
    return false
  }
}

// Check if local storage is available
function isLSAvail() {
  try {
    if (typeof(window.localStorage) !== "undefined") {
      return true;
    }
  } catch (error) {
    console.log('browser storage not available');
  }
  return false;
}

// Check if session storage is available
function isSSAvail() {
  try {
    if (typeof(window.localStorage) !== "undefined") {
      return true;
    }
  } catch (error) {
    console.log('browser storage not available');
  }
  return false;
}
</script>